import React from 'react';
import PropTypes from 'prop-types';

const trackClick = (content) => {
  if (typeof ga !== 'undefined') {
    console.log(ga);
    ga('send', 'click', 'external link', 'download dev edition', content);
  }
};

const DownloadLink = props => (
  <a
    href={`https://www.mozilla.org/firefox/developer/?utm_source=moz-playground&utm_medium=referral&utm_content=${props.content}`}
    target="_blank"
    rel="noopener noreferrer"
    onClick={trackClick(props.content)}
  >
    {props.children}
  </a>
);

DownloadLink.propTypes = {
  content: PropTypes.string.isRequired,
};

export default DownloadLink;



